import React, { useState }  from 'react';
import './Textmain.css';


const Textmain = (props) => {
    const handleUpClick =() =>{
        let newText = text.toUpperCase();
        settext(newText)
    }
    const handleLoClick =() =>{
        let newText = text.toLowerCase();
        settext(newText)
    }
    const handleClearClick =() =>{
        let newText = '';
        settext(newText)
    }
    const handleCopy = () =>{
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
    }
    const handleExtraSpaces = () =>{
        let newText = text.split(/[ ]+/);
        settext(newText.join(" "))
    }
    const handleOnChange =(event) =>{
        settext(event.target.value)
    }
const [text, settext] = useState('');
    return (
       <>
       <div className="container my-3">
            <div className="mb-3">
            <h4>Type Here</h4>
            <textarea className="form-control" value={text} onChange={handleOnChange} id="myBox" rows="8" ></textarea>
            <button className="btn btn-primary my-3" onClick={handleUpClick}>Convert To UPPERCASE</button>
            <button className="btn btn-primary my-3 mx-3" onClick={handleLoClick}>Convert To lowercase</button>
            <button className="btn btn-outline-danger my-3 mx-3" onClick={handleClearClick}>Clear Text</button>
            <button className="btn btn-outline-success my-3 mx-3" onClick={handleCopy}>Copy Text</button>
            <button className="btn btn-outline-primary my-3 mx-3" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
            </div>

       </div>
       <div className="container">
           <h1>Your Text Summary</h1>
           <h6>{text.split(" ").length} Words and {text.length} Characters</h6>
           <h2>Preview</h2>
           <p>{text}</p>
       </div>
       </>
    )
}

export default Textmain
